import { createHashHistory } from 'history';
import React from 'react';
import { Route, Router, Switch } from 'react-router-dom';
import { Game } from 'sections/Main/Game/Game';

export const Main: React.FC = () => {
  const history = createHashHistory();

  return (
    <Router history={history}>
      <Switch>
        <Route path="/page-1" exact={true} component={() => <DummyPage pageNumber={1} />} />
        <Route path="/page-2" exact={true} component={() => <DummyPage pageNumber={2} />} />
        <Route path="/page-3" exact={true} component={() => <DummyPage pageNumber={3} />} />
        <Route path="/" component={Game} />
      </Switch>
    </Router>
  );
};

const DummyPage: React.FC<{ pageNumber: number }> = ({ pageNumber }) => {
  return (
    <div className="w-100 text-center">
      <h1>Page-{pageNumber}</h1>
      <a href={`#/`}>HOME</a>
    </div>
  );
};
